body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body .header {
  /* margin-top: 100px; */
  font-family: cursive;
}
.nav {
  background: #000;
  height: 3rem;
  font-size: 2rem;
  font-family: cursive;
  color: white;
  text-align: center;
}

.board {
  display: flex;
  height: 30rem;
  width: 30rem;
  margin: auto;
  border: 1px solid black;
  flex-wrap: wrap;
}
.top {
  display: flex;
  justify-content: space-between;
  padding: 1rem;
  /* margin-top: 40px; */
  padding: 0.5rem;
  font-size: 1rem;
  width: 29rem;
  /* background: #fee; */
  margin: auto;
}
.square {
  width: 10rem;
  height: 10rem;
  background: #fee;
  /* border: 1px solid black; */
  outline: 2px solid black;
  outline-offset: -2px;
}

.square .heading {
  font-size: 4rem;
  margin: 30px;
  color: rgb(25, 29, 4);
}
.square .heading2 {
  font-size: 4rem;
  margin: 30px;
  color: rgb(93, 184, 8);
}
.square:hover {
  background: rgb(240, 141, 141);
  cursor: pointer;
}

.reset {
  border: 1px solid red;
  color: red;
  border-radius: 5px;
  width: 30rem;
  font-size: 2rem;
  margin-top: 20px;
  height: 70px;
  background: #fff;
}
.reset:hover {
  background: red;
  color: white;
}
@media only screen and (max-width: 500px) {
  .board {
    height: 18rem;
    width: 18rem;
  }
  .square {
    width: 6rem;
    height: 6rem;
    outline: 1px solid black;
    outline-offset: -1px;
  }
  .top {
    margin-top: 20px;
    justify-content: space-between;
    padding: 1rem;
    padding: 0.5rem;
    font-size: 1rem;
    width: 18rem;
    font-family: cursive;
  }

  .square .heading {
    font-size: 2rem;
    margin: 30px;
    color: rgb(25, 29, 4);
  }
  .square .heading2 {
    font-size: 2rem;
    margin: 30px;
    color: rgb(93, 184, 8);
  }
  .reset {
    border: 1px solid red;
    color: red;
    border-radius: 5px;
    width: 18rem;
    font-size: 1rem;
    margin-top: 20px;
    height: 50px;
    background: #fff;
  }
}
